.nav-nav {
  max-width: 320px;
  margin: 0 auto;
}
.nav-ul {
  display: flex;
  gap: 8px;
  font-size: 2rem;
  border-bottom: 1px solid #536471;
}
.nav-ul > li {
  padding: 8px;
  border-radius: 50%;
  cursor: pointer;
}
.nav-ul > li:hover {
  background-color: rgba(231, 233, 234, 0.1);
}
